  font-weight: 300;
  font-family: Roboto, sans-serif;
  color: #34343c; }

a {
  font-family: Roboto, sans-serif;
  color: #00bcd4;
  outline: 0; }
  a:focus, a:hover, a:active {
    outline: 0;
    color: #00bcd4; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 300;
  font-family: Roboto, sans-serif;
  color: #34343c;
  line-height: 1.4;
