    h1 > a:focus, h2 > a:focus, h3 > a:focus, h4 > a:focus, h5 > a:focus, h6 > a:focus {
      text-decoration: none; }

::selection {
  color: #fff;
  background: #00bcd4;
  text-shadow: none; }

::-webkit-selection {
  color: #fff;
  background: #00bcd4;
  text-shadow: none; }

::-moz-selection {
